<template>
  <el-container class="home-container">
    <!-- Â§¥ÈÉ®Âå∫Âüü -->

    <el-header>
      <div>
        <img src="../assets/touxiang.jpg" alt />
        <span>ÁîµÂïÜÂêéÂè∞ÁÆ°ÁêÜÁ≥ªÁªü</span>
      </div>
      <el-button type="info" @click="quit">ÈÄÄÂá∫</el-button>
    </el-header>
    <!-- ‰∏ª‰ΩìÂå∫Âüü -->
    <el-container>
      <!-- ‰æßËæπÊ†è -->
      <el-aside :width="isCollapse ? '64px' : '200px'">
        <div class="toggle-btns" @click="toggleCollapse">|||</div>
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#ffd04b"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          router
          :default-active="activePath"
        >
          <!-- ‰∏ÄÁ∫ßËèúÂçï -->
          <el-submenu :index="item.id + ''" :key="item.id" v-for="item in menulist">
            <!-- ‰∏ÄÁ∫ßËèúÂçïÁöÑÊ®°ÊùøÂå∫Âüü -->
            <template slot="title">
              <!-- ÂõæÊ†á -->
              <i :class="iconsObj[item.id]"></i>
              <!-- ÊñáÊú¨ -->
              <span>{{`${item.authName}`}}</span>
            </template>

            <!-- ‰∫åÁ∫ßËèúÂçï -->
            <el-menu-item
              :index="`/${subItem.path}`"
              v-for="subItem in item.children"
              :key="subItem.id"
              @click="saveNavState(`/${subItem.path}`)"
            >
              <template slot="title">
                <!-- ÂõæÊ†á -->
                <i class="el-icon-menu"></i>
                <!-- ÊñáÊú¨ -->
                <span>{{`${subItem.authName}` }}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!-- Âè≥‰æßÂå∫Âüü -->
      <el-main>
        <!-- Ë∑ØÁî±Âç†‰ΩçÁ¨¶ -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      menulist: [],
      iconsObj: {
        '125': 'iconfont icon-user',
        '103': 'iconfont icon-tijikongjian',
        '101': 'iconfont icon-shangpin',
        '102': 'iconfont icon-danju',
        '145': 'iconfont icon-baobiao'
      },
      //ÊòØÂê¶ÊäòÂè†
      isCollapse: false,
      //Ë¢´ÊøÄÊ¥ªÁöÑËøûÊé•Âú∞ÂùÄ
      activePath: ''
    }
  },
  created() {
    this.getMenuList()
    this.activePath = window.sessionStorage.getItem('activePath')
  },
  methods: {
    quit() {
      //Ê∏ÖÁ©∫token
      window.sessionStorage.clear()
      //Ë∑≥ËΩ¨Âà∞ÁôªÈôÜÈ°µ
      this.$router.push('/login')
    },
    // Ëé∑ÂèñÊâÄÊúâ‰∫åÁ∫ßÁöÑËèúÂçï
    async getMenuList() {
      const { data: res } = await this.$http.get('menus')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.menulist = res.data
      console.log(res)
    },
    //ÁÇπÂáªÊåâÈíÆÔºåÂàáÊç¢ËèúÂçïÁöÑÊäòÂè†‰∏éÂ±ïÂºÄ
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    },
    // ‰øùÂ≠òËøûÊé•ÁöÑÊøÄÊ¥ªÁä∂ÊÄÅ
    saveNavState(activePath) {
      window.sessionStorage.setItem('activePath', activePath)
      this.activePath = activePath
    }
  }
}
</script>

