<template>
  <div class="login_container">
    <div class="login_box">
      <!-- Â§¥ÂÉèÂå∫Âüü -->
      <div class="avatar_box">
        <img src="../assets/logo.png" />
      </div>
      <!-- ÁôªÈôÜË°®ÂçïÂå∫ -->
      <el-form
        ref="LoginFromRef"
        :model="LoginForm"
        class="login_input"
        :rules="LoginFormRules"
        label-width="0px"
      >
        <!-- Áî®Êà∑Âêç -->
        <el-form-item prop="username">
          <el-input v-model="LoginForm.username" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <!-- ÂØÜÁ†Å -->
        <el-form-item prop="password">
          <el-input
            v-model="LoginForm.password"
            prefix-icon="iconfont icon-3702mima"
            type="password"
          ></el-input>
        </el-form-item>
        <!-- ÊåâÈíÆÂå∫Âüü -->
        <el-form-item class="btns">
          <el-button type="primary" @click="Login">ÁôªÈôÜ</el-button>
          <el-button type="info" @click="LoginFormReset">ÈáçÂà∂</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      LoginForm: {
        username: 'admin',
        password: '123456'
      },
      // ÁôªÂΩïË°®ÂçïÊ†°È™åËßÑÂàô
      LoginFormRules: {
        // Áî®Êà∑ÂêçÊ†°È™åËßÑÂàô
        username: [
          { required: true, message: 'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç', trigger: 'blur' },
          { min: 3, max: 5, message: 'ÈïøÂ∫¶Âú® 3 Âà∞ 5 ‰∏™Â≠óÁ¨¶', trigger: 'blur' }
        ],
        // ÂØÜÁ†ÅÊ†°È™åËßÑÂàô
        password: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å', trigger: 'blur' },
          { min: 6, max: 15, message: 'ÈïøÂ∫¶Âú® 6 Âà∞ 15 ‰∏™Â≠óÁ¨¶', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // ÁÇπÂáªÈáçÁΩÆÊåâÈíÆÔºåÂÜçÂÖÖÂÄºÁôªÈôÜË°®Âçï
    LoginFormReset() {
      this.$refs.LoginFromRef.resetFields()
    },
    Login() {
      this.$refs.LoginFromRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('login', this.LoginForm)
        if (res.meta.status !== 200) return this.$message.error('ËØ∑Ê£ÄÊü•Ë¥¶Âè∑ÂØÜÁ†ÅÊòØÂê¶Ê≠£Á°ÆÔºÅ')
        this.$message.success('üéâÁôªÈôÜÊàêÂäüÔºÅ')
        // 1.Â∞ÜÁôªÈôÜÊâøËØ∫‰πãÂêéÁöÑtokenÔºå‰øùÂ≠òÂà∞ÂÆ¢Êà∑Á´ØÁöÑsessionStorage‰∏≠
        //  1.1È°πÁõÆ‰∏≠Èô§‰∫ÜÁôªÈôÜ‰πãÂ§ñÁöÑÂÖ∂‰ªñAPIÊé•Âè£ÔºåÁ¢ßÁé∫Âú®ÁôªÈôÜ‰πãÂêéÊâçËÉΩËÆøÈóÆ
        //  1.2tokenÂè™Â∫îÂú®ÂΩìÂâçÁΩëÁ´ôÊâìÂºÄÊúüÈó¥ÁîüÊïàÔºåÊâÄ‰ª•Â∞Ütoken‰øùÂ≠òÂà∞sessionStorage‰∏≠
        window.sessionStorage.setItem('token', res.data.token)
        // ÈÄöËøáÁºñÁ®ãÂºèÂØºËà™Ë∑≥ËΩ¨Âà∞ÂêéÂè∞‰∏ªÈ°µÔºåË∑ØÁî±Âú∞ÂùÄÊòØ/home
        this.$router.push('/home')
      })
    }
  }
}
</script>

